/* Modal Improvements */
.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    min-height: 100vh; /* Add this */
    min-height: -webkit-fill-available; /* Add this for iOS Safari */
    background: rgba(0, 0, 0, 0.6);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
    backdrop-filter: blur(5px);
    overflow: hidden !important; 
}

.modal-content {
    font-family: 'Inter', sans-serif;
    background: white;
    padding: 25px 25px 25px 25px; /* Reduced padding to save space */
    border-radius: 16px;
    border: 3px solid #b85c38;
    width: 90%;
    max-width: 500px; /* Increased to accommodate 2 columns */
    text-align: center;
    box-shadow: 0 20px 60px rgba(0,0,0,0.3);
    animation: modalSlide 0.3s ease;
    max-height: 80vh !important;
    overflow-y: auto !important;
    position: relative; /* For absolute positioning of close button */
}

@keyframes modalSlide {
    from {
        opacity: 0;
        transform: scale(0.8);
    }
    to {
        opacity: 1;
        transform: scale(1);
    }
}

.modal-content h2 {
    font-family: 'Poppins', sans-serif;
    font-weight: 600;
    color: #b85c38;
    margin-bottom: 8px; /* Reduced spacing */
    margin-top: 5px; /* Reduced since image is above */
    font-size: 18px; /* Smaller font */
}

.modal-buttons {
    display: flex;
    gap: 12px; /* Slightly reduced */
    margin-top: 20px; /* Reduced spacing */
}

.modal-buttons.single-button {
    justify-content: center;
    margin-top: 20px; /* Reduced spacing */
}

.modal-buttons.single-button .modal-add-button {
    width: 100%;
    max-width: 220px; /* Slightly reduced */
    padding: 12px 20px; /* Reduced padding */
    font-size: 1rem; /* Slightly smaller */
}

.modal-cancel-button, .modal-add-button {
    font-family: 'Poppins', sans-serif;
    flex: 1;
    padding: 10px 18px; /* Reduced padding */
    border: none;
    border-radius: 8px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.2s ease;
    font-size: 0.95rem; /* Slightly smaller */
}

.modal-cancel-button {
    background: #dc3545;
    color: white;
}

.modal-cancel-button:hover {
    background: #c82333;
    transform: translateY(-1px);
}

.modal-add-button {
    background: #28a745;
    color: white;
}

.modal-add-button:hover {
    background: #218838;
    transform: translateY(-1px);
}

/* Modal Item Image Styles */
.modal-item-image-container {
    margin-bottom: 0px;
    text-align: center;
}

.modal-item-image {
    width: 100px; /* Reduced size */
    height: 100px; /* Reduced size */
    object-fit: cover;
    border-radius: 12px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
    border: 3px solid #b85c38;
    transition: all 0.3s ease;
}

.modal-item-image:hover {
    transform: scale(1.05);
    box-shadow: 0 6px 20px rgba(0, 0, 0, 0.3);
}

/* Enhanced Input Styles */
.kiosk-container input[type="text"] {
    font-family: 'Inter', sans-serif;
    width: 100%;
    padding: 15px;
    margin-bottom: 18px;
    border: 2px solid #e0e0e0;
    border-radius: 8px;
    font-size: 1rem;
    font-weight: 400;
    box-sizing: border-box;
    transition: all 0.3s ease;
}

.kiosk-container input[type="text"]:focus {
    border-color: #b85c38;
    box-shadow: 0 0 0 3px rgba(184, 92, 56, 0.1);
    outline: none;
    transform: translateY(-1px);
}

.kiosk-form button {
    font-family: 'Poppins', sans-serif;
    background: #b85c38;
    color: #fff;
    border: none;
    padding: 12px 12px;
    border-radius: 8px;
    font-size: 1rem;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
}

.kiosk-form button:hover {
    background: #a14d2c;
    transform: translateY(-1px);
}

.kiosk-container label {
    font-family: 'Inter', sans-serif;
    margin-left: 8px;
    font-size: 1rem;
    font-weight: 500;
}

.kiosk-container input[type="checkbox"], .kiosk-container input[type="radio"] {
    accent-color: #b85c38;
}

/* Step 2 - Enhanced Order Details */
.back-button-container {
    display: flex;
    align-items: flex-start;
    margin-bottom: 0px;
}

.back-button {
    font-family: 'Poppins', sans-serif !important;
    background: linear-gradient(135deg, #fff 0%, #f8f9fa 100%) !important;
    border: 2px solid #b85c38 !important;
    color: #b85c38 !important;
    font-size: 1rem !important;
    font-weight: 600 !important;
    cursor: pointer !important;
